body,
button,
a,
input,
select {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

*,
::before,
::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.content {
  max-width: 1160px;
  padding: 0 34px;
  margin: 0 auto;
  @media (max-width: 550px) {
    padding: 0 20px;
  }
}

input {
  border: none;
  background-color: transparent;
  outline: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
  background: none;
  outline: none;
}
